import React from "react";
import "./Intro.css";
import Github from '../../img/github.png';
import LinkedIn from '../../img/linkedin.png';
import Instagram from '../../img/instagram.png';
import Vector1 from "../../img/Vector1.png";
import Vector2 from "../../img/Vector2.png";
import boy from "../../img/boy.png";
import glassesimoji from "../../img/glassesimoji.png";
import thumbup from "../../img/thumbup.png";
import crown from "../../img/crown.png";
import FloatingDiv from "../../FloatingDiv/FloatingDiv";
import { themeContext } from "../../Context";
import { useContext } from "react";
import { motion } from "framer-motion";

const Intro = () =>{
  //Transistion
  const transistion ={ duration:2,type:"spring"};
    // context
    const theme = useContext(themeContext);
    const darkMode = theme.state.darkMode;
return(
<div className="intro">
    <div className="i-left">

    
    <div className="i-name">
        <span style={{ color: darkMode ? "white" : ""}}>HEY! I am</span>
        <span>Nayan Savaria</span>
        <span>Lorem, ipsum dolor sit amet consectetur adipisicing elit. Ducimus ipsa odio libero maiores nihil aliquam quis ex dolores quas ab.</span>
    </div>
    
    <button className="button i-button">Hire me</button>
 
    <div className="i-icons">
      <a href="https://github.com/NayanSavaria?tab=repositories">
          <img src={Github} alt="" />
          </a>
           <a href="https://www.linkedin.com/in/nayan-savaria-b11446205/">  <img src={LinkedIn} alt=""/>
           </a>
          <a href="https://www.geeksforgeeks.org/how-to-redirect-to-another-page-in-reactjs/">
          <img src={Instagram} alt="" />
          </a>
        </div>

    </div>
    <div className="i-right">
     <img src={Vector1} alt="" />
     <img src={Vector2} alt="" />
     <img src={boy} alt="" />

    <motion.img initial={{left:"-36%"}} whileInView={{left: "-24%"}}
    transition={transistion} src={glassesimoji} alt=""/>
     
     <motion.div initial={{top:"-4%", left:"74%"}} whileInView={{left:"68%"}}
     transistion={transistion} className="floating-div">
     <FloatingDiv img={crown} text1="Web" text2="Developer" /> 
     </motion.div>
     <motion.div
          initial={{ left: "9rem", top: "18rem" }}
          whileInView={{ left: "0rem" }}
          transistion={transistion}
          className="floating-div"
        >
       <FloatingDiv img={thumbup} text1='Best Design' text2='Always'/> 

     </motion.div>
     <div className="blur" style={{ background: "rgb(238 210 255)" }}></div>
     <div
          className="blur"
          style={{
            background: "#C1F5FF",
            top: "17rem",
            width: "21rem",
            height: "11rem",
            left: "-9rem",
          }}
        ></div>
    </div>
</div>
);
};

export default Intro;